import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios);
Vue.config.productionTip = false
var moment = require('moment');
moment().format();
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
Vue.filter("timeF",(t)=>{
  return moment(t).startOf('minute').fromNow().replace(/hours?/, '').
  replace('ago', '').
  replace(/days?/, '').
  replace(/minutes?/, '').
  replace(/\ban?/, '1').
  replace(/months?/, '')
      .replace(/\byears?/, '')
      .replace(/\s/g, '')
      .replace('fewseconds','');
})
